

import React from 'react';

class Login extends React.Component
{
 render()
 {
     return(
        <div>
           <form>
             <label>Username</label>
             <input type ="text"  />
             <br/>

             <label>Password</label>
             <input type="password" />
              <br/>

              <button>Login</button>

           </form>



        </div>

     )
 }

}
export default Login;